import React, { Component } from 'react';
import { connect } from "react-redux";
import { LoadVacations } from '../State/actions';
import Vacation from './Vacation'


class UserInterface extends Component {

    componentDidMount(){
        this.props.loadVacations();
    }

    render() {
        return (
            <div className="UserInterface">
                <div className="col-xl-8 col-lg-11 col-md-11 col-sm-1 mx-auto mt-lg-5">
                    <div className="container text-center">
                        <h1>New Vacations</h1>
                    </div>
                    {this.props.vacations.map(v => <Vacation key={v.id} v={v} />)}
                </div>
            </div>
        );
    }
}
const mapStateToProps = state => {
   
    return { vacations: state.vacations };
};

const mapDispatchToProps = dispatch => {
   
    return {
        loadVacations: () => {
           
            return dispatch(LoadVacations());
            
        }
    }
};

const userInterface = connect(mapStateToProps, mapDispatchToProps)(UserInterface);
export default userInterface;
