import React, { Component } from 'react';
import { BrowserRouter } from 'react-router-dom';

import './App.css';
import 'font-awesome/css/font-awesome.min.css';
import Home from './Components/Home';
import Footer from './Components/Footer';
import UserInterface from './Components/UserUi/UserInterface';
import Admin from './Components/AdminUi/Admin';



class App extends Component {
  state = {
    isLogged: false,
    role: ''
  }

  componentDidMount() {

    fetch('http://localhost:3000/Checking').then(r => r.json()).then(data => {
      this.setState({ isLogged: data.msg, role: data.role });
      debugger;
    })
  }

  render() {
    if (!this.state.isLogged && this.state.role === "user") {
      return (
        <React.Fragment>
          <UserInterface />
          <Footer />
        </React.Fragment>
      )
      
    }
    else if (!this.state.isLogged && this.state.role === "Admin") {
      return (
        <React.Fragment>
          <Admin />
          <Footer />
        </React.Fragment>
      )

    }
    else {
      return (
        <BrowserRouter>
          <div className="App">
            <Home />
            <Footer />
          </div>
        </BrowserRouter>
      );
    }
  }
}

export default App;
