const initialState = {
    vacations: []
};

const vacationReducer = (state = initialState, action) => {
    let newState = {};
    switch (action.type) {

        case 'VACATION_LOADED':

        newState = Object.assign({}, state, {
            vacations: action.data
        });
        return newState;
        default:
            return state;
    }
};
export default vacationReducer;