import React, { Component } from 'react';
import { Link } from 'react-router-dom';


class Login extends Component {
  state = {
    username: '',
    password: ''
  }

  render() {
    return (
      <div className="col-xl-5 col-lg-6 col-md-10 col-sm-12 mx-auto mt-lg-5">
        <div className="card">
          <div className="card-body">
            <h3>Login Page</h3>
            <div className="md-form">
              <i className="fa fa-user prefix"></i>
              <input onChange={this.handleChange.bind(this)} name="username" type="text" placeholder="Username" className="form-control" />
            </div>
            <hr />
            <div className="md-form">
              <i className="fa fa-lock prefix"></i>
              <input onChange={this.handleChange.bind(this)} name="password" type="password" placeholder="password" className="form-control" />
            </div>
            <div className="text-center">
              <button className="btn btn-primary" onClick={this.login.bind(this)}>Login</button>
              <hr />
              Don't have an account?
              <Link to="/register" id="linkB"> Sign up!</Link>
            </div>
          </div>
        </div>
      </div>
    );
  }

  handleChange(ev) {
    this.setState({ [ev.target.name]: ev.target.value })
  }

  async login() {
    if (this.state.username === '') {
      alert('Username required!');
    }
    else if (this.state.password === '') {
      alert('password required!');
    }
    else {
      let res = await fetch('http://localhost:3000/signin', {
        method: 'POST',
        headers: {
          'Accept': 'application/json',
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(this.state)
      });
      const content = await res.json();
      if (content.type === "error") {
        alert('Username or password is incorrect!');
      }
      else {
        debugger;
        if (content.role === "Admin") {
          alert(`You are logged in as ${content.role}`);
          this.props.history.push("/admin");
        }
        else {
          alert(`welcome back ${content.name}`);
          this.props.history.push("/user");
        }
      }
    }
  }
}
export default Login;
