import React, { Component } from 'react';
import { Switch, Route } from 'react-router-dom';
import Login from './Login';
import Register from './Register';
import userInterface from './UserUi/UserInterface';
import Admin from './AdminUi/Admin';

class Home extends Component {
    render() {
        return (
            <Switch>
                 <Route  path="/user" component={userInterface}/>
                 <Route  path="/admin" component={Admin}/>
            <div className="page-header">
                <div className="container text-center">
                    <div>
                        <h1 className="title">Trip Tours</h1>
                        <h4 className="description">Your Vacation Starts Here...</h4>
                    </div>
                    <div className="row">
                        <Route exact path="/" component={Login}/>
                        <Route path="/login" component={Login}/>
                        <Route path ="/register" component={Register}/>
                   
                    </div>
                </div>
            </div>
            </Switch>
        );
    }
}

export default Home;
